@import "Colors.css";


/* ------------ IMPORTANTE --------------*/
.container{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  max-width: 100%;
}

/* ----------- COLORES ---------------*/

#Dashboard{
  margin-top: 100px !important;
  margin-bottom: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center ;
}

#card_pie{
  margin: auto;
  width: 600px;
}

#image_button_col{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-shrink: auto;
}

#web_info_card{
  border-radius: 3px;
  padding-top: 5vh;
  padding-left: 3vh;
  background-color: white;
  margin-bottom: 10px;
}

#big_logo {
  max-width:40vw;
}

#image_button_container{
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: center;
}

#image_button{
  margin: 10px;
}


@media (min-width:320px)  { /* smartphones, iPhone, portrait 480x320 phones */ }

@media (max-width:641px)  {
  #image_button{
    width: 100vw;
  }
}

/* For desktop: */
@media only screen and (min-width: 1025px) {


  #big_logo {
    max-width:10vh;
  }

  #image_button{
    display: flex;
    width: 18rem;
    height: 10rem;
  }

  #web_info_card{
    max-width: 40vw;
    padding: 5vh;
  }

  #block_info{
    margin-left: 15px;
  }

}
