@import "Colors.css";

/* ------------ IMPORTANTE --------------*/
.rc-time-picker-input{
  font-size: 16px!important;
  height: 40px!important;
  color: black!important;
}

.rc-time-picker-clear{
  line-height: 30px!important;
}

.indicator-time{
  display: flex;
  align-items: flex-end;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: -15px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.indicator-time_inner{
  font-size: 1.2rem!important;
  padding: 40px;
}

.indicator-time_percentage{
font-size: 1.6rem!important;
}

.indicator-question{
  display: flex;
  align-items: flex-end;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: -40px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.indicator-question_inner{
  font-size: 1.2rem!important;
  padding: 40px;
} 

.indicator-question_percentage{
  font-size: 1.6rem!important;

}

#container_preguntas{
    border-width: 1px;
    border-radius: 4px;
    background: white;
    overflow-y: auto;
    padding: 1 1 1 1;
}

#container_respuestas_test{
  flex: auto;
  align-content: flex-start;
  align-items: baseline;
}

#card_pregunta_test_header{
  font-size: 115%;
  text-align: center;
}

#card_pregunta_test{
  margin-bottom: 10px;
}

/* For listofonos*/
#component_test{
  height: 100vh;
  width: 95%;
  padding-top: 100px;
  margin: auto;
  margin-bottom: 100px;
}

#test_config{
  margin: auto;
  width: 100%;
  height: 40vh;
  margin-bottom: 10px;
}

#container_preguntas{
  margin: auto;
  padding-top: 10px;
  margin-top: 10px;
  width: 100vw;
  height: 80vh;
  margin-bottom: 30px;
}

#test_start_button{
  margin-top: 60px!important;
  width: 50vw!important;
}

#form_element_dropdown{
  margin-bottom:  10px;
  width: 85vw;
  z-index: 99;
}

#container_test_estado{
  display: flex;
  margin: auto;
  flex-direction: row;
  justify-content: center;
}


/* Para listofonos pequennos*/
@media only screen and (max-width: 480px) {
  #test_estado{
    visibility: collapse;
  }
  
  #container_preguntas{
    height: 95vh;
  }

}

@media (min-width:961px)  { 
  

}


/* For desktop: */
@media only screen and (min-width: 768px) {
  #component_test{
    margin: auto;
    padding-top: 100px;
    padding-left: 10px;
    max-width: 100vw;
    max-height: 80vh;
  }

  #form_element_dropdown{
    width: 25vw;
    margin-bottom: 10px;
  }

  #test_config{
    margin-left: 100px;
    height: 40vh;
  }

  #test_estado{
    margin: auto;
    visibility: visible;
    width: auto;
    height: auto;
  }

  #container_preguntas{
    margin: auto;
    margin-left: -20px;
    max-width: 65vw;
    height: 82vh;
  }

  #test_start_button{
    margin-top: 60px!important;
    width: 20vw!important;
  }

  #test_estado_body{
    display: flex;
    align-items: center;
    padding: 10px;
    align-items: center;
  }
}
